.scrolltop {
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative; }

.scrolltop {
  z-index: 2;
  position: fixed;
  right: 3vw;
  bottom: 6vh;
  width: 40px;
  height: 40px;
  font-weight: bold;
  color: #ffffff;
  border: 2px solid #ffffff;
  background-color: #ffd2a9;
  border-radius: 50%;
  user-select: none;
  cursor: pointer;
  opacity: 0.7;
  transition-property: opacity scale border color background-color;
  transition-duration: 0.4s; }
  .scrolltop:hover {
    opacity: 1;
    transform: scale(1.1, 1.1);
    border: 2px solid #ffd2a9;
    color: #ffd2a9;
    background-color: #ffffff; }
  .scrolltop.hide {
    opacity: 0;
    cursor: default; }
  .scrolltop svg {
    width: 60%;
    height: 60%; }
