.front, .back__content {
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative; }

.card {
  cursor: pointer;
  position: relative;
  width: 100%;
  padding-bottom: 40%;
  margin: 5px;
  transform-style: preserve-3d;
  user-select: none; }
  .card:hover .front {
    transform: rotateY(-180deg); }
  .card:hover .back {
    transform: rotateY(0deg); }

.front,
.back {
  position: absolute;
  width: inherit;
  height: 100%;
  overflow: hidden;
  backface-visibility: hidden;
  transition: 1s; }

.front {
  transform: rotateY(0deg); }
  .front > img {
    height: 80%;
    width: 80%; }

.back {
  background: #f1f1f1;
  transform: rotateY(180deg); }

.back__content {
  width: inherit;
  height: 100%;
  color: #2c3e50;
  flex-direction: column;
  padding: 10px;
  /* > h2 {
    font-size: 1.8em;
    font-weight: 700;
    margin-bottom: 20px;
  } */
  /* > span {
    font-size: 1.25em;
    margin-bottom: 15px;
  } */ }
  .back__content .icons > a {
    margin: 0 5px;
    width: 40px;
    height: 40px;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    color: #2c3e50;
    font-size: 1.6em;
    transition: 0.4s;
    border-radius: 50%; }
  .back__content .icons a:hover {
    background: #2c3e50;
    color: #fff; }
