.avatar img {
  position: absolute;
  left: -100%;
  right: -100%;
  top: -100%;
  bottom: -100%;
  margin: auto;
  width: 100%;
  height: auto; }
  .avatar img.landscape {
    width: auto;
    height: 100%; }

.profile {
  margin-left: auto;
  margin-right: auto;
  width: 90%;
  min-width: 320px -20px;
  max-width: 640px;
  overflow: hidden; }
  @media only screen and (min-width: 1248px) {
    .profile {
      padding: 85px 20px 30px; } }
  @media only screen and (max-width: 1247px) {
    .profile {
      padding: 85px 20px 30px; } }
  @media only screen and (max-width: 767px) {
    .profile {
      padding: 80px 15px 15px; } }
  @media only screen and (max-width: 559px) {
    .profile {
      padding: 60px 10px 10px; } }
  @media only screen and (max-width: 399px) {
    .profile {
      padding: 50px 5px 5px; } }
  .profile .wrapper {
    margin-left: auto;
    margin-right: auto;
    max-width: 560px;
    box-shadow: 3px 3px 5px rgba(255, 223, 240, 0.3);
    text-shadow: 3px 3px 3px rgba(255, 223, 240, 0.2); }

.avatar {
  background-color: #ffffff;
  position: relative;
  margin-left: auto;
  margin-right: auto; }
  .avatar .frame {
    padding-top: 75%; }
  .avatar img {
    width: 90%;
    transition-duration: 0.5s; }

.info {
  margin-top: -1px;
  position: relative;
  padding: 0.5rem 1rem 1rem;
  text-align: center;
  background-color: #ffffff; }
  .info h2 {
    margin: 0;
    font-weight: bold;
    letter-spacing: 1px; }
  .info p {
    margin: 10px;
    text-align: justify;
    text-justify: inter-ideograph;
