.app, .header,
.footer, .header .main, .header .nav ul, .link, .link > * {
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative; }

.fixBG {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background-size: cover;
  background-position: center;
  background: url(/static/images/background/Protruding-Squares.svg) repeat;
  z-index: -100;
  /* &::after {
    @include pseudo();
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    background: $fixBGMask;
    opacity: 0.7;
  } */ }

.app {
  flex-direction: column;
  min-height: 100vh; }

.wrapper {
  margin-left: auto;
  margin-right: auto;
  width: 100%; }

.header,
.footer {
  width: 100%;
  color: #ffffff;
  background: #ffd2a9; }
  .header .wrapper,
  .footer .wrapper {
    padding: 0.2rem;
    display: flex;
    align-items: center; }

.header {
  z-index: 4;
  position: fixed;
  top: 0;
  transform: translateY(0);
  opacity: 0.8;
  transition: 0.3s cubic-bezier(0.47, 1.04, 1, 1);
  /* @include responsive-height(60px, 55px, 50px, 45px);

  & + .content {
    @include responsive-marginTop(60px, 55px, 50px, 45px);
  } */ }
  .header:hover, .header.top {
    opacity: 1; }
  .header.hide {
    transform: translateY(-100%); }
  .header .wrapper {
    justify-content: space-between; }
  .header .main .logo {
    position: relative;
    width: 40px;
    padding-top: 100%;
    background-image: url(/static/icons/logo.svg);
    background-origin: center center;
    background-size: contain;
    background-repeat: no-repeat;
    cursor: pointer;
    transition-duration: 0.5s; }
    .header .main .logo:hover {
      opacity: 0.8; }
  .header .nav ul li {
    flex: 1;
    font-weight: bold;
    cursor: pointer;
    margin-left: 5px; }
    .header .nav ul li:first-of-type {
      margin-left: 0; }
    .header .nav ul li a {
      transition: background-color 0.3s ease;
      display: block;
      padding: 10px;
      color: inherit;
      white-space: nowrap;
      text-decoration: none; }
      .header .nav ul li a:hover {
        background-color: #d9d9d9; }
    .header .nav ul li.active a {
      text-decoration: underline; }

.content {
  flex: 1;
  width: 100%; }
  .content .wrapper {
    overflow: hidden; }

.footer {
  z-index: 3;
  position: fixed;
  left: 0;
  bottom: 0; }
  .footer .wrapper {
    justify-content: center;
    flex-direction: column; }
  .footer b {
    font-weight: bold; }
    .footer b {
      font-size: 12px; }
    @media (min-width: 320px) and (max-width: 1024px) {
      .footer b {
        font-size: calc( 12px + 4 * (100vw - 320px) / 704); } }
    @media (min-width: 1024px) {
      .footer b {
        font-size: 16px; } }

.link {
  margin-left: 0.5rem; }
  .link > * {
    margin-left: 0.5rem;
    color: #ffffff;
    background: #ffd2a9;
    border-radius: 2rem;
    cursor: pointer;
    transition: 0.3s ease; }
    @media only screen and (min-width: 1248px) {
      .link > * {
        width: 40px; } }
    @media only screen and (max-width: 1247px) {
      .link > * {
        width: 40px; } }
    @media only screen and (max-width: 767px) {
      .link > * {
        width: 35px; } }
    @media only screen and (max-width: 559px) {
      .link > * {
        width: 30px; } }
    @media only screen and (max-width: 399px) {
      .link > * {
        width: 20px; } }
    @media only screen and (min-width: 1248px) {
      .link > * {
        height: 40px; } }
    @media only screen and (max-width: 1247px) {
      .link > * {
        height: 40px; } }
    @media only screen and (max-width: 767px) {
      .link > * {
        height: 35px; } }
    @media only screen and (max-width: 559px) {
      .link > * {
        height: 30px; } }
    @media only screen and (max-width: 399px) {
      .link > * {
        height: 20px; } }
    .link > *:hover {
      color: #ffd2a9;
      background: #ffffff; }
    .link > * svg {
      width: 80%;
      height: 80%; }
