.hero, .hero > * .mask {
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative; }

.hero {
  flex-direction: column;
  justify-content: space-around;
  flex-wrap: wrap;
  align-content: center;
  width: 100%;
  height: 92vh;
  user-select: none; }
  @media only screen and (max-width: 559px) {
    .hero {
      height: 95vh; } }
  .hero > * {
    position: relative;
    overflow: hidden;
    margin: 2.5px;
    background-color: white;
    border: 5px solid #ffffff;
    cursor: pointer; }
    .hero > * .bg {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: 100%;
      height: 100%;
      background-color: #ffd2a9;
      background-size: cover;
      background-position: center center;
      background-repeat: no-repeat; }
      @media only screen and (max-width: 559px) {
        .hero > * .bg {
          background-position-y: 60%; } }
    .hero > * .mask {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -170%);
      width: 100%;
      height: 100%;
      background-color: rgba(255, 255, 255, 0.7);
      transition: all 0.4s ease; }
      .hero > * .mask p {
        font-size: 36px;
        font-weight: bold; }
    .hero > *:hover .bg {
      filter: blur(5px); }
    .hero > *:hover .mask {
      transform: translate(-50%, -50%); }
    .hero > *:nth-child(1), .hero > *:nth-child(2) {
      flex: 1;
      height: 50%;
      width: 50%; }
    .hero > *:nth-child(3), .hero > *:nth-child(4) {
      float: right;
      width: 25%;
      height: 100%; }
    @media only screen and (max-width: 559px) {
      .hero > *:nth-child(1), .hero > *:nth-child(2), .hero > *:nth-child(3), .hero > *:nth-child(4) {
        float: none;
        flex: 1;
        width: 100%;
        height: 50%; } }
