.skills {
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative; }

.skills {
  margin-left: auto;
  margin-right: auto;
  justify-content: space-around;
  flex-wrap: wrap;
  align-content: center;
  width: 90%;
  min-width: 320px -20px;
  max-width: 1248px;
  overflow: hidden; }
  @media only screen and (min-width: 1248px) {
    .skills {
      padding: 50px; } }
  @media only screen and (max-width: 1247px) {
    .skills {
      padding: 50px; } }
  @media only screen and (max-width: 767px) {
    .skills {
      padding: 40px; } }
  @media only screen and (max-width: 559px) {
    .skills {
      padding: 30px; } }
  @media only screen and (max-width: 399px) {
    .skills {
      padding: 20px; } }
  .skills > * {
    flex: 1;
    transition: 0.3s ease; }
    @media only screen and (max-width: 559px) {
      .skills > * {
        flex: 0 1 50%; } }
    .skills > * path {
      transition: 0.3s ease; }
    .skills > *:hover {
      transform: scale(1.1); }
      .skills > *:hover path:last-of-type {
        stroke-dasharray: 100; }
      .skills > *:hover text {
        font-weight: 600; }
