.singleChart .circularChart {
  display: block;
  margin: 10px auto;
  max-width: 80%;
  max-height: 250px;
  user-select: none; }

.singleChart .AI {
  fill: #3b3323; }

.singleChart .AE {
  fill: #312963; }

.singleChart .PR {
  fill: #3a2641; }

.singleChart .PS {
  fill: #2d2668; }

.singleChart .circleBg {
  fill: none;
  stroke: #d9d9d9;
  stroke-width: 3.8; }

.singleChart .circle {
  fill: none;
  stroke-width: 2.8;
  stroke-linecap: round;
  animation: progress 1s ease-in-out forwards; }

.singleChart .circularChart.AI .circle {
  stroke: #fc9d0e; }

.singleChart .circularChart.AE .circle {
  stroke: #c88fff; }

.singleChart .circularChart.PS .circle {
  stroke: #90c5fc; }

.singleChart .circularChart.PR .circle {
  stroke: #d757ff; }

.singleChart .percentage {
  fill: #fff;
  font-family: sans-serif;
  font-size: 0.5em;
  text-anchor: middle; }
  .singleChart .percentage.AI {
    fill: #fc9d0e; }
  .singleChart .percentage.AE {
    fill: #c88fff; }
  .singleChart .percentage.PS {
    fill: #90c5fc; }
  .singleChart .percentage.PR {
    fill: #d757ff; }

@keyframes progress {
  0% {
    stroke-dasharray: 0 100; } }
